'use client';

import { useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { CheckCircleIcon, MapPinIcon, UserPlusIcon, UsersIcon } from "lucide-react";
import { useAuth } from "@/contexts/AuthContext";
import { useFriends, useRecommendedUsers, useOutgoingRequests, useFriendActions } from "@/hooks/useFriends";
import { capitialize } from "@/lib/utils";
import NoFriendsFound from "@/components/ui/NoFriendFound";
import FriendCard from "@/components/ui/FriendCard";
import { getLanguageFlag } from "@/components/ui/FriendCard";

export default function HomePage() {
	const { user } = useAuth();

	// Custom hooks for data management
	const { friends, loadingFriends, fetchFriends } = useFriends();
	const { recommendedUsers, loadingUsers, fetchRecommendedUsers } = useRecommendedUsers();
	const { outgoingRequestsIds, fetchOutgoingRequests } = useOutgoingRequests();
	const { sendingRequest, handleSendFriendRequest } = useFriendActions();

	// Initial data fetch when user is available
	useEffect(() => {
		if (user) {
			fetchFriends();
			fetchRecommendedUsers();
			fetchOutgoingRequests();
		}
	}, [user, fetchFriends, fetchRecommendedUsers, fetchOutgoingRequests]);

	// Callback after friend request is sent
	const onFriendRequestSent = () => {
		fetchOutgoingRequests(); // Refresh outgoing requests to update UI
	};

	return (
		<div className="p-4 sm:p-6 lg:p-8">
			<div className="container mx-auto space-y-10">

				{/* FRIENDS SECTION */}
				<section>
					{/* Header */}
					<div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 mb-6">
						<h2 className="text-2xl sm:text-3xl font-bold tracking-tight">Your Friends</h2>
						<Link href="/notifications" className="btn btn-outline btn-sm">
							<UsersIcon className="mr-2 size-4" />
							Friend Requests
						</Link>
					</div>

					{/* Friends List */}
					{loadingFriends ? (
						<div className="flex justify-center py-12">
							<span className="loading loading-spinner loading-lg" />
						</div>
					) : friends.length === 0 ? (
						<NoFriendsFound />
					) : (
						<div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
							{friends.map((friend) => (
								<FriendCard key={friend._id} friend={friend} />
							))}
						</div>
					)}
				</section>

				{/* RECOMMENDED USERS SECTION */}
				<section>
					{/* Header */}
					<div className="mb-6 sm:mb-8">
						<div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4">
							<div>
								<h2 className="text-2xl sm:text-3xl font-bold tracking-tight">Meet New Learners</h2>
								<p className="text-base-content opacity-70 mt-1">
									Discover perfect language exchange partners based on your profile
								</p>
							</div>
						</div>
					</div>

					{/* Recommended Users List */}
					{loadingUsers ? (
						<div className="flex justify-center py-12">
							<span className="loading loading-spinner loading-lg" />
						</div>
					) : recommendedUsers.length === 0 ? (
						<div className="card bg-base-200 p-6 text-center">
							<h3 className="font-semibold text-lg mb-2">No recommendations available</h3>
							<p className="text-base-content opacity-70">
								Check back later for new language partners!
							</p>
						</div>
					) : (
						<div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
							{recommendedUsers.map((user) => {
								const hasRequestBeenSent = outgoingRequestsIds.has(user._id);
								const isCurrentlySending = sendingRequest === user._id;

								return (
									<div
										key={user._id}
										className="card bg-base-200 hover:shadow-lg transition-all duration-300"
									>
										<div className="card-body p-5 space-y-4">
											{/* User Profile */}
											<div className="flex items-center gap-3">
												<div className="avatar size-16 rounded-full overflow-hidden relative">
													<Image
														src={user.profilePic}
														alt={user.fullName}
														fill
														className="object-cover"
														sizes="64px"
														priority={false}
													/>
												</div>

												<div className="flex-1 min-w-0">
													<h3 className="font-semibold text-lg truncate">{user.fullName}</h3>
													{user.location && (
														<div className="flex items-center text-xs opacity-70 mt-1">
															<MapPinIcon className="size-3 mr-1 flex-shrink-0" />
															<span className="truncate">{user.location}</span>
														</div>
													)}
												</div>
											</div>

											{/* Languages with flags */}
											<div className="flex flex-wrap gap-1.5">
												<span className="badge badge-secondary text-xs">
													{getLanguageFlag(user.nativeLanguage)}
													Native: {capitialize(user.nativeLanguage)}
												</span>
												<span className="badge badge-outline text-xs">
													{getLanguageFlag(user.learningLanguage)}
													Learning: {capitialize(user.learningLanguage)}
												</span>
											</div>

											{/* Bio */}
											{user.bio && (
												<p className="text-sm opacity-70 line-clamp-3">
													{user.bio}
												</p>
											)}

											{/* Action button */}
											<button
												className={`btn w-full mt-2 ${hasRequestBeenSent ? "btn-disabled" : "btn-primary"
													}`}
												onClick={() => handleSendFriendRequest(user._id, onFriendRequestSent)}
												disabled={hasRequestBeenSent || isCurrentlySending}
											>
												{isCurrentlySending ? (
													<>
														<span className="loading loading-spinner loading-xs mr-2" />
														Sending...
													</>
												) : hasRequestBeenSent ? (
													<>
														<CheckCircleIcon className="size-4 mr-2" />
														Request Sent
													</>
												) : (
													<>
														<UserPlusIcon className="size-4 mr-2" />
														Send Friend Request
													</>
												)}
											</button>
										</div>
									</div>
								);
							})}
						</div>
					)}
				</section>

			</div>
		</div>
	);
}